html {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Source Sans Pro", sans-serif;
  background-color: var(--main-bg);
  color: var(--text-dark);
  margin-block-end: 50vh;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

figure,
blockquote {
  margin: 0;
}

header ul,
footer ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}
.visually-hidden:focus {
  left: 0;
  height: auto;
  width: auto;
  background-color: #fff;
  border: 1px solid;
  padding: 0.5rem;
}

.hover-fade:hover {
  opacity: 0.9;
}

h1,
h2,
h3 {
  font-weight: 700;
  margin-top: 0;
}

h1 {
  font-size: 2.625rem;
  font-size: clamp(2.625rem, 1.77rem + 3.64vw, 4.5rem);
  line-height: 1;
  margin-block-end: 1.25rem;
}

h2 {
  font-size: 2.25rem;
  font-size: clamp(2.25rem, 1.91rem + 1.45vw, 3rem);
  line-height: 1.1;
  margin-block-end: 1.25rem;
}

h3 {
  font-size: 1.5rem;
  line-height: 1;
}

p {
  margin-top: 0;
  font-size: 1.125rem;
  line-height: 1.3;
}
p.medium {
  font-size: 1.25rem;
  font-size: clamp(1.25rem, 1.14rem + 0.48vw, 1.5rem);
  margin-block-end: 2.5rem;
}
p.large {
  font-size: 1.5rem;
  font-size: clamp(1.5rem, 1.39rem + 0.48vw, 1.75rem);
  margin-block-end: 2.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --main-bg: hsla(0, 0%, 100%, 1);
  --text-dark: hsla(233, 14%, 25%, 1);
  --text-light: hsla(0, 0%, 100%, 1);
  --text-link-hover: hsla(320, 85%, 41%, 1);
  --header-bg: hsla(232, 58%, 55%, 1);
  --hero-bg: hsla(232, 58%, 55%, 1);
  --button-primary-bg: hsla(180, 100%, 42%, 1);
  --button-primary-bg-hover: hsla(180, 100%, 37%, 1);
  --button-primary-white-bg: hsla(0, 0%, 100%, 1);
  --button-primary-white-bg-hover: hsla(0, 0%, 100%, 0.8);
  --button-primary-text: hsla(233, 14%, 25%, 1);
  --button-secondary-bg: transparent;
  --button-secondary-bg-hover: hsla(0, 0%, 100%, 1);
  --button-secondary-border: hsla(0, 0%, 100%, 1);
  --button-secondary-text: hsla(0, 0%, 100%, 1);
  --button-secondary-text-hover: var(--hero-bg);
  --button-cta-bg: hsla(0, 0%, 100%, 1);
  --button-cta-text: hsla(233, 14%, 25%, 1);
  --fullwidth-bg: hsla(320, 85%, 41%, 1);
  --cta-gradient-bg: linear-gradient(
    90deg,
    hsla(233, 14%, 25%, 1) 0%,
    hsla(232, 58%, 55%, 1) 76.28%,
    hsla(180, 100%, 42%, 1) 152.28%
  );
}
@media (min-width: 43.75em) {
  :root {
    --cta-gradient-bg: linear-gradient(
      90deg,
      hsla(233, 14%, 25%, 1) 0%,
      hsla(232, 58%, 55%, 1) 56.34%,
      hsla(180, 100%, 42%, 1) 112.47%
    );
  }
}

.button {
  display: inline-block;
  font-size: 1.125rem;
  line-height: 1;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  padding: 0.625rem 0.875rem;
  border-radius: 1.5rem;
  transition-property: background-color, color;
  transition-duration: 200ms;
  transition-timing-function: ease-in-out;
}
.button.primary {
  background-color: var(--button-primary-bg);
  color: var(--button-primary-text);
  padding: 0.75rem 1rem;
}
.button.primary:hover {
  background-color: var(--button-primary-bg-hover);
}
.button.primary.white {
  background-color: var(--button-primary-white-bg);
}
.button.primary.white:hover {
  background-color: var(--button-primary-white-bg-hover);
}
.button.secondary {
  background-color: var(--button-secondary-bg);
  border: 2px solid var(--button-secondary-border);
  color: var(--button-secondary-text);
}
.button.secondary:hover {
  background-color: var(--button-secondary-bg-hover);
  color: var(--button-secondary-text-hover);
}

.fullwidth {
  color: var(--text-light);
  text-align: center;
}
.fullwidth__description {
  max-width: 70ch;
  margin-inline: auto;
}
.fullwidth__image {
  margin-inline: auto;
}

.logo {
  width: 8rem;
}
@media (max-width: 38.62375em) {
  .logo.sm-mobile {
    width: 4.375rem;
  }
}

.wrapper {
  width: min(100% - 3rem, 75rem);
  margin-inline: auto;
  padding-block: 60px;
}
@media (min-width: 56.25em) {
  .wrapper {
    padding-block: 80px;
  }
}

.topnav {
  background-color: var(--header-bg);
}
.topnav__wrapper {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: space-between;
  align-items: center;
  padding-block: 12px;
}
@media (min-width: 56.25em) {
  .topnav__wrapper {
    padding-block: 20px;
  }
}
.topnav__wrapper nav {
  justify-self: end;
}
.topnav__homelink {
  justify-self: start;
}
.topnav__links {
  display: grid;
  grid-template-columns: repeat(3, auto);
  gap: 20px;
}
@media (min-width: 56.25em) {
  .topnav__links {
    gap: 40px;
  }
}
.topnav__link {
  font-size: 1rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: var(--text-light);
}
.topnav__link:hover {
  text-decoration: underline;
  text-decoration-thickness: 2px;
  text-decoration-color: var(--text-light);
  text-underline-offset: 4px;
}

.hero {
  background-color: var(--hero-bg);
  color: var(--text-light);
}
.hero__wrapper {
  display: grid;
  grid-template-columns: 1fr;
  gap: 40px;
  justify-items: center;
  align-items: center;
  padding-block: 30px 60px;
}
@media (min-width: 56.25em) {
  .hero__wrapper {
    grid-template-columns: 6fr 5fr;
    gap: 10%;
    padding-block: 40px 80px;
  }
}
.hero__image {
  width: 53.5vw;
  max-width: 30.1875rem;
}
@media (min-width: 56.25em) {
  .hero__image {
    order: 2;
    width: 100%;
  }
}
.hero__button.primary {
  margin-inline-end: 20px;
}

.features__wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(min(100%, 21.625rem), 1fr));
  gap: 40px;
  justify-items: center;
}
.features__item {
  text-align: center;
}
@media (min-width: 56.25em) {
  .features__item {
    text-align: start;
  }
}
.features__icon {
  margin-block-end: 10px;
  margin-inline: auto;
}
@media (min-width: 56.25em) {
  .features__icon {
    margin-inline: 0;
  }
}
.features__title {
  margin-block-end: 0.625rem;
}
.features__description {
  max-width: 50ch;
  margin-block-end: 0;
}

.fw-feature {
  background-color: var(--fullwidth-bg);
}

.testimonial__wrapper {
  max-width: 50rem;
}
.testimonial__icon {
  width: clamp(3.25rem, 2.625rem + 2.8vw, 4.875rem);
  margin-block-end: clamp(1.25rem, 0.85rem + 2vw, 2.5rem);
}
.testimonial__quote {
  font-size: 1.5rem;
  font-size: clamp(1.5rem, 1.16rem + 1.45vw, 2.25rem);
  line-height: 1.25;
  font-weight: 700;
  margin-block-end: 2.5rem;
}
.testimonial__author-wrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, 7.5rem);
  gap: 20px 30px;
  align-items: center;
}
@media (max-width: 38.62375em) {
  .testimonial__author-wrapper {
    justify-content: center;
  }
}
.testimonial__author-image {
  width: 7.5rem;
  border-radius: 50%;
}
@media (max-width: 38.62375em) {
  .testimonial__author-image {
    grid-column: span 3;
    justify-self: center;
  }
}
.testimonial__author-description {
  grid-column: span 3;
}
@media (max-width: 38.62375em) {
  .testimonial__author-description {
    text-align: center;
  }
}
.testimonial__author-description .medium {
  margin-block-end: 0;
}

.fw-cta {
  background-image: var(--cta-gradient-bg);
}
.footer__wrapper {
  display: flex;
  gap: 2.5rem 1.25rem;
  flex-wrap: wrap;
}
.footer__logo {
  flex: 100%;
}
.footer__homelink {
  display: block;
  width: fit-content;
}
.footer__logo-image {
  margin-block-end: 0.625rem;
}
.footer__copyright {
  line-height: 1;
  margin-block-end: 0;
}
.footer__grid {
  display: grid;
  gap: 2.5rem 1.25rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 43.75em) {
  .footer__grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.footer__column {
  flex: calc(50% - 0.625rem);
}
@media (min-width: 43.75em) {
  .footer__column {
    flex: auto;
  }
}
.footer__column-title {
  font-size: 1.375rem;
  line-height: 1.1;
  margin-block-end: 0.875rem;
}
.footer__link {
  display: inline-flex;
  align-items: center;
  gap: 1rem;
  color: var(--text-dark);
  font-size: 1.125rem;
  line-height: 1;
  margin-block-end: 0.625rem;
}
.footer__link:hover {
  color: var(--text-link-hover);
}
.footer__link:hover .footer__social-path {
  fill: var(--text-link-hover);
}
.footer__social {
  width: 1.125rem;
}
.footer__social-path {
  fill: var(--text-dark);
}